import React from 'react'
import {Link} from 'react-router-dom'
import {HeaderList, NavBarItem} from '../styles'
import BackArrow from '../images/back_arrow.png'

const CalculateHeader = () => (
        <HeaderList>
            <NavBarItem><Link to="/"><img src={BackArrow} style={{width:'30px'}} alt="Back" /></Link></NavBarItem>
            <NavBarItem>Back to Home</NavBarItem>
        </HeaderList>
)

export default CalculateHeader